@charset "utf-8";
/*导入字体*/
@font-face {
  font-family: 'dianshu';
  src: url('../font/dianshu.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}
.dianshu {
  font-family: 'dianshu';
}
/* CSS Document wlo:Cflower */
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  border: 0 none;
  font: inherit;
  margin: 0;
  padding: 0;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
ol,
ul {
  list-style: outside none none;
}
blockquote,
q {
  quotes: none;
}
blockquote::before,
blockquote::after,
q::before,
q::after {
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  outline: 0 none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
input,
textarea {
  border-radius: 0;
  outline: 0 none;
  resize: none;
  -webkit-appearance: none;
  /*去掉iOS下input的默认内阴影*/
}
.clearfix:after,
.clearfloat:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: "";
  clear: both;
  height: 0;
}
html {
  font-family: "Microsoft YaHei", 'Tahoma', 'Helvetica', 'PingFangSC-Regular', "Heiti SC", "sans-serif";
  -webkit-text-size-adjust: none;
}
body {
  margin: 0 auto;
  min-width: 3.2rem;
  position: relative;
  width: 100%;
  height: 100%;
  background: #001218;
}
a {
  text-decoration: none;
  color: inherit;
}
.clearfloat:after {
  clear: both;
  content: "";
  display: block;
  height: 0;
  visibility: hidden;
}
.ridius {
  border-radius: 5px;
}
/* 假如添加了video标签,可能会出现video标签默认置顶的情况,解决这个bug可以通过样式设置来规避;*/
.compatibleStyle {
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
  /* Chrome 和 Safari */
  -moz-backface-visibility: hidden;
  /* Firefox */
  -ms-backface-visibility: hidden;
  /* Internet Explorer */
  -webkit-perspective: 0;
  -webkit-transform: translate3d(0, 0, 0);
  visibility: visible;
}
/*弹出层遮罩div*/
.black_overlay {
  display: none;
  position: absolute;
  top: 0%;
  left: 0%;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: #000;
  z-index: 988;
  -moz-opacity: 0.5;
  opacity: 0.5;
  filter: alpha(opacity=50);
}
.btn {
  display: block;
  cursor: pointer;
}
.pos {
  position: absolute;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
.w750 {
  width: 100%;
}
.curp {
  cursor: pointer;
}
.imgcove {
  width: 100%;
  height: 100%;
  display: block;
  border: none;
}
.yellow {
  color: #ffd800;
}
.hide {
  display: none;
}
.show {
  display: block;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
/*=========================== dialog通用样式开始 ====================================*/
#maskLayer {
  display: none;
  position: fixed;
  z-index: 10000;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  margin: 0 auto;
  height: 100%;
  background: #040001;
  opacity: 0.9;
  filter: alpha(opacity=90);
}
#alertInfo {
  display: none;
  background: rgba(255, 255, 255, 0);
  left: 50%;
  top: -100%;
  position: absolute;
  z-index: 10001;
  width: 100%;
  max-width: 7.5rem;
}
#alertInfo .close,
.layer_close {
  position: absolute;
  right: 0.4rem;
  top: -0.3rem;
  transform: rotate(-45deg);
  overflow: hidden;
  display: block;
  width: 0.7rem;
  height: 0.7rem;
  background: url(../images/pop/pop_close.png) no-repeat center center;
  background-size: 0.7rem 0.7rem;
  z-index: 999;
  cursor: pointer;
  text-indent: -999em;
}
/* ==========弹出框========== */
.pop {
  width: 100%;
  margin: 0 auto;
  position: relative;
  overflow: hidden;
}
/*弹出层*/
.pop_warp_img,
.popytbVideo {
  width: 100%;
  height: 4.22rem;
}
.popytbVideo video {
  width: 100%;
  height: 100%;
}
.news_lists {
  height: 13.5rem;
}
/* 公共部分结束 */
#wrap {
  width: 7.5rem;
  height: auto;
  /* background: #fff; */
  position: relative;
  overflow: hidden;
  margin: 0 auto;
}
