@-webkit-keyframes run {
  from {
    background-position: 0 0;
  }
  to {
    background-position: 0 100%;
  }
}
@keyframes run {
  from {
    background-position: 0 0;
  }
  to {
    background-position: 0 100%;
  }
}
@-webkit-keyframes unrun {
  from {
    background-position: 0 100%;
  }
  to {
    background-position: 0 0;
  }
}
@keyframes unrun {
  from {
    background-position: 0 100%;
